//Tags

//bigTag
.tag1 {
  font-size: 10px;

  padding: 2px 1.25rem;
}

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    0deg,
    rgb(0, 0, 0.8),
    rgba(53, 53, 53, 0.7),
    rgba(211, 211, 211, 0.09),
    transparent,
    transparent
  );
}
//Inside Image Tag

.prod-tag {
  position: absolute;
  bottom: 0;
  left: 0;
  height: auto;
  color: $white;
  z-index: 5;
  .tag {
    display: inline;
    background-color: $brand;
    padding: 4px 15px;
  }
  .tag-title {
    font-size: $small-heading;
    font-weight: $font-weight-semibold;
    // line-height: 20px;
    margin-top: 10px;
  }
  .tag-date {
    font-size: 10px;
    line-height: 20px;
  }
}

// Simple Tag
.tag-out {
  display: inline-block;
  font-size: $copy-right;
  color: $white;
  background-color: $tag;
  padding: 5px 10px;
  margin-right: 10px;
}

//Small Cards
// .card

.bread-crumb {
  //   position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-bottom: 10px solid $brand;
  box-sizing: border-box;
  color: $white;
  font-size: $content;
  .bread-crumb-title {
    padding: 7px 15px;
    display: inline-block;
    background-color: $brand;
  }
}

section {
  margin-bottom: 100px;
}

