%hero_face {
  width: 67.9375rem;
  height: 57.5rem;
  overflow: hidden;
}
%side_face {
  width: 35rem;
  height: 28.375rem;
  overflow: hidden;
}
%small_news {
  width: 11.25rem;
  height: 7.5rem;
  overflow: hidden;
  position: relative;
}
%trending_left_face {
  width: 50.625rem;
  height: 51.25rem;
  overflow: hidden;
}
%trending_right_top_face {
  // width: 50.625rem;
  height: 28.375rem;
  overflow: hidden;
  position: relative;
}
%trending_right_bot_face {
  width: 25.125rem;
  height: 22.5rem;
  overflow: hidden;
  position: relative;
}
.home-banner {
  height: 57.18rem;

  .home-hero-img-wrapper {
    @extend %hero_face;
    position: relative;
    overflow: hidden;
    img {
      height: inherit;
    }
  }

  //   @extend %side_face;
  .home-ch-row {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .home-hero-2 {
      .home-hero-2-img-wrapper {
        @extend %side_face;
        position: relative;
        overflow: hidden;
        img {
          height: inherit;
        }
      }
    }
  }

  .home-hero-3 {
    @extend %side_face;
  }
}

// Hot news
.hot-news {
  .video-session {
    width: 24.87rem;
    height: 18.25rem;
    iframe {
      width: 100%;
      height: 100%;
    }
  }
  .follow-us-session {
    width: 25rem;
    background-color: $follow-us;
    padding: 20px 30px;
    border-top: 0.625rem solid $brand;
    h2 {
      font-size: $small-heading;
      font-weight: $font-weight-bold;
      line-height: 3rem;
      color: $brand;
    }
    p {
      font-size: $copy-right;
      text-align: justify;
    }
    .icons {
      color: $brand;
    }
  }
}

//Trendings
.trending {
  .trend-container {
    height: 51.25rem;
    .card-left {
      @extend %trending_left_face;
      img {
        height: inherit;
      }
    }
    .card-right-top {
      @extend %trending_right_top_face;
      margin: 0;
      img {
        height: inherit;
        width: 100%;
      }
    }
    .two {
      display: flex;
      flex-direction: column;
      // justify-content: space-between;
      .second {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        .card-right-bot {
          @extend %trending_right_bot_face;
          img {
            height: inherit;
          }
        }
      }
    }
  }
}
