@media screen and (max-width: 1855px) {
  :root {
    font-size: 13.333px;
  }
}
@media screen and (max-width: 1600px) {
  :root {
    font-size: 12px;
  }
}
@media screen and (max-width: 1440px) {
  :root {
    font-size: 10px;
  }
}
@media screen and (max-width: 1200px) {
  :root {
    font-size: 8.6px;
  }
  .navbar-toggler {
    width: 70px;
    height: 40px;
    .navbar-toggler-icon {
      font-size: 16px;
    }
  }

  .navbar-collapse {
    // position: absolute;
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-start;
    ul {
      position: relative;
      width: 100%;
      display: flex;
      // width: 100%;
      justify-content: center;
      margin: 0 !important;
      li {
        text-align: center;
      }
    }
    form {
      flex-flow: row nowrap;
    }
  }

  footer {
    // background: red;
    .sections {
      .card {
        .card-body {
          .card-title {
            font-size: 1rem;
          }
          .card-text {
            font-size: 1.3rem;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 991px) {
  :root {
    font-size: 15px;
  }
  footer {
    // background: red;
    .sections {
      .card {
        .card-body {
          .card-title {
            font-size: 1rem !important;
          }
          .card-text {
            font-size: 0.7rem;
          }
        }
      }
      .nav {
        .nav-item {
          a {
            font-size: 0.8rem;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 768px) {
  :root {
    font-size: 12.4px;
    // }
    // footer {
    //   // background: red;
    //   .sections {
    //     h5 {
    //       font-size: 1rem;
    //     }
    //   }
    // }
  }
  @media screen and (max-width: 420px) {
    :root {
      font-size: 8.5px;
    }

    footer {
      .container {
        .sections {
          justify-content: center;
          .nav {
            .nav-item {
              .nav-link {
                font-size: 1.5rem;
              }
            }
          }
        }
      }
    }
  }
}
