footer {
  width: 100%;
  height: auto;
  background-color: $brand;
  color: $white;
  .container {
    .sections {
      // border-right: 1px solid #ccc;
      // overflow: hidden;
      padding: 20px;

      // .sections:last-child {
      //   border: none;
      // }
      h2 {
        margin-bottom: 25px;
        font-weight: $font-weight-semibold;
        font-size: $small-heading;
      }
      ul {
        li {
          display: inline-block;

          a {
            font-size: 1rem;
            color: $white;
            white-space: nowrap;
            i {
              font-size: 1.25rem;
            }
          }
        }
      }

      .card {
        background-color: transparent;
        border: 0px;
        color: $white;
        margin-bottom: 20px;
        .card-body {
          padding: 10px 20px;
          .card-title {
            // color: $soft-text;
            font-size: 1.7rem;
            line-height: 5px;
            font-weight: $font-weight-bold;
          }
        }
      }
    }
  }
  .copyright-container {
    background-color: rgba(0, 0, 0, 0.15);
    .copy-right {
      font-size: $copy-right;
    }
  }
}
