.navbar {
  padding: 3rem 0 0;
  height: 8rem;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.2);
  .container {
    .navbar-brand {
      font-size: $heading;
      font-weight: $font-weight-black;
      color: $brand !important;
    }

    .collapse {
      .navbar-nav {
        %active {
          // background-color: $brand;
          // color: $white;
          // font-weight: $font-weight-semibold;
          border-left: 5px solid $brand;
        }
        .nav-item {
          a {
            font-size: $nav-item;
            color: $brand;

            transition: 0.3s ease-in-out;
            padding: 5px 10px;
          }

          a.active {
            @extend %active;
          }
        }
        .nav-item:hover a {
          @extend %active;
        }

        form {
          flex-flow: row wrap;
          input {
            width: 20rem;
          }
          .search-icon {
            font-size: 2rem;
            color: $brand;
          }
        }
      }
    }
  }
}
